.container {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px 30px 20px;
}
@media (max-width: 841px) {
  .container {
    padding: 0 20px 10px 20px;
  }
}
.buttonWrapper {
  display: flex;
  align-items: center;
  background-color: #f7f7f9;
  z-index: 1;
}

.button {
  border: none;
  text-decoration: none;
  cursor: pointer;
  color: #00002e;
  font-size: 22px;
  font-weight: bold;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 16px 10px 18px 10px;
  margin: 15px 0 0 15px;
}
@media (max-width: 1000px) {
  .button {
    display: none;
  }
}
.button:hover {
  background-color: #0000381a;
  height: 25px;
  border-radius: 6px;
}

.dateListScrollable {
  display: flex;
  overflow-x: scroll;
  scrollbar-width: none;
  margin: 2px 0 2px -40px;
  -webkit-overflow-scrolling: touch;
}
@media (max-width: 1000px) {
  .dateListScrollable {
    margin: 0;
  }
}
.dateListScrollable::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
}

.monthContainer {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  padding: 2px;
  margin: 2px;
}

