.monthYearLabel {
  align-self: flex-start;
  font-family: "Manrope bold";
  font-size: 12px;
  font-weight: 800;
  position: sticky;
  top: 10px;
  left: 50px;
  width: max-content;
  margin: 0 60px;
  color: #0000384d;
  text-transform: capitalize;
}
@media (max-width: 1000px) {
  .monthYearLabel {
    font-size: 10px;
    left: 0;
  }
}
.dateDayItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin: 0 0 0 5px;
  width: 45px;
  flex-shrink: 0;
}
@media (max-width: 841px) {
  .dateDayItem {
    width: 30px;
  }
}
.dateDayItem:hover {
  background-color: #0000380d;
  border-radius: 6px;
}
.daysContainer {
  display: flex;
  z-index: 1;
  margin: 8px 0;
}
@media (max-width: 1000px) {
  .daysContainer {
    margin-left: 0 !important;
  }
}
.dayLabel {
  font-size: 14px;
  font-weight: 500;
  margin: 4px 0 0 0;
}
@media (max-width: 841px) {
  .dayLabel {
    font-size: 11px;
  }
}
.dateLabel {
  font-size: 18px;
  font-family: "Manrope semibold";
  font-weight: 600;
}
@media (max-width: 841px) {
  .dateLabel {
    font-size: 13px;
  }
}
.dayLabel {
  color: rgba(0, 0, 56, 0.3);
}
.markedLabel {
  margin-top: 10px;
}

.calendar {
  width: 55px;
  height: 55px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 22px;
}
.calendar img {
  max-width: 90%;
}
@media (max-width: 841px) {
  .calendar {
    width: 40px;
    height: 40px;
  }
}
.amount {
  font-size: 14px;
  padding-bottom: 5px;
  font-family: "Manrope bold";
}
@media (max-width: 841px) {
  .amount {
    font-size: 10px;
  }
}
.weekend {
  background-color: rgba(209, 65, 55, 0.05);
}
.calendar {
  cursor: pointer;
}
